@import url('https://fonts.googleapis.com/css?family=Lato:400,700,800,900');

$screen-xl-point: 1248px;
$screen-md-point: 768px;

$yellow: #EBFF00;
$transparentYellow: #EBFF0099;
$pink: #FF70D9;
$black: #000000;
$white: #FFFFFF;

$headerHeight: 80px;

@mixin offers__title {
  font-weight: 800;
  letter-spacing: 0em;
  text-align: left;
  color: $white;
}

@mixin offers__subtitle {
  margin: 22px 0;
  font-weight: 800;
  letter-spacing: 0em;
  text-align: left;
  color: $white;
}

@mixin offers__title_xl {
  @include offers__title;
  
  max-width: 634px;
  font-size: 74px;
  line-height: 74px;
}

@mixin offers__title_xl_small {
  @include offers__title;
  
  max-width: 297px;
  font-size: 46px;
  line-height: 46px;
}


@mixin offers__title_md {
  @include offers__title;
  
  max-width: 232px;
  font-size: 40px;
  line-height: 40px; 
}

@mixin offers__subtitle_xl {
  @include offers__subtitle;

  font-size: 26px;
  line-height: 31px;  
}

@mixin offers__subtitle_xl_small {
  @include offers__subtitle;

  font-size: 24px;
  line-height: 29px;
}

@mixin offers__subtitle_md {
  @include offers__subtitle;

  font-size: 16px;
  line-height: 19px;  
}


@mixin heading1 {
  font-weight: 800;
  line-height: 72px;
  font-size: 72px;

  color: $white;
}

@mixin heading2 {
  font-weight: 800;
  font-size: 32px;
  line-height: 38px;

  color: $white;
}

@mixin paragraph {
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
}

p,
h1,
h2,
button,
label,
li
div {
  margin: 0;
  font-family: 'Lato';
  letter-spacing: 0em;
}

.header {
  position: fixed;
  top: 0;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: $headerHeight;
  background-color: $black;

  @media (max-width: $screen-md-point) {
    height: 48px;
  }
}


.header__logo {
  width: 104px;
  height: 35px;
  margin-left: 81px;

  @media (max-width: $screen-xl-point) {
    width: 71px;
    height: 24px;
    margin-left: 30px;
  }

  @media (max-width: $screen-md-point) {
    margin-left: 20px;
  }
}

.header__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-right: 91px;

  @media (max-width: $screen-xl-point) {
    padding-right: 50px;
  }

  @media (max-width: $screen-md-point) {
    display: none;
  }
}

.header__menu-item {
  padding: 0 15px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;

  &:last-child {
    padding: 0;
    padding-left: 15px;
  }

  color: $white;

  &:hover {
    cursor: pointer;
    background-clip: text;
    -webkit-text-fill-color: transparent;
    background-image: linear-gradient(98.25deg, $yellow 18.56%, $pink 76.02%);
  }
}

.header__cart {
  width: 24px;
  height: 20px;
  background-image: url(./assets/header-cart.svg);

  &:hover {
    background-image: url(./assets/header-gradient-cart.svg);
  }
}

.banner {
  min-height: 500px;
  background: url(./assets/earth.svg),
    linear-gradient(180deg,   rgba(0, 0, 0, 0) -0.52%, rgba(15, 20, 32, 0.79) 99.48%),
    url(./assets/banner.svg) ;
  background-repeat: no-repeat;
  background-position: calc(100% - 36px) 139px, center, center;
  background-size: 327px 367px, cover, cover;

  @media (max-width: $screen-xl-point) {
    background-size: 300px 336px, cover, cover;
    background-position: calc(100% + 100px) 106px, center, center;
  }

  @media (max-width: $screen-md-point) {
    min-height: 499px;
    background-size: 300px 336px, cover, cover;
    background-position: 50% -57px, center, center;
  }
}

.banner__content {
  max-width: 600px;
  padding: 171px 0 119px 82px;

  @media (max-width: $screen-xl-point) {
    max-width: 411px;
    padding: 180px 0 100px 30px;
  }

  @media (max-width: $screen-md-point) {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 298px 34px 44px 34px;
    max-width: none;
  }
}

.banner__title {
  @include heading1;

  @media (max-width: $screen-xl-point) {
    font-size: 48px;
    line-height: 48px ;
  }

  @media (max-width: $screen-md-point) {
    font-size: 36px;
    line-height: 36px ;
  }
}

.banner__title_pink {
  color: $pink;
}

.banner__subtitle {
  @include heading2;

  margin: 22px 0;
}

.banner__subtitle_yellow {
  color: $yellow;
}

.offers {
  margin: 58px 80px 0 80px;

  @media (max-width: $screen-xl-point) {
    margin: 55px 29px 0 30px;
  }

  @media (max-width: $screen-md-point) {
    margin: 55px 20px 0 20px;
  }
}

.offers__heading {
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 800;
  line-height: 38px;
  letter-spacing: 0em;
  text-align: left;
  color:$white;
}

.offers__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;

  @media (max-width: $screen-xl-point) {
    gap: 30px;
  }

  @media (max-width: $screen-md-point) {
    grid-template-columns: 1fr;
  }
}

.offers__item {
  padding: 35px 0 37px 35px;

  border-radius: 10px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  &:nth-of-type(1) {
    grid-column-start: 1;
    grid-column-end: 3;

    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%), 
      url(./assets/offer-1.svg);

    @media (max-width: $screen-xl-point) {
      grid-column-end: 2;
      padding: 106px 0 27px 27px;
    }

    @media (max-width: $screen-md-point) {
      grid-column-end: 1;
    }

    & .offers__title {
      @include offers__title_xl;

      @media (max-width: $screen-xl-point) {
        @include offers__title_md;
      }
    }

    & .offers__subtitle{
      @include offers__subtitle_xl;

      @media (max-width: $screen-xl-point) {
        @include offers__subtitle_md;
      }
    }

  }

  &:nth-of-type(2) {
    padding: 50px 0 35px 35px;

    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
      url(./assets/offer-2.svg);

    @media (max-width: $screen-xl-point) {
      padding: 106px 0 27px 27px;
    }
    
    & .offers__title {
      @include offers__title_xl_small;

      @media (max-width: $screen-xl-point) {
        @include offers__title_md;
      }
    }

    & .offers__subtitle {
      @include offers__subtitle_xl_small;

      @media (max-width: $screen-xl-point) {
        @include offers__subtitle_md;
      }
    }
  }

  &:nth-of-type(3) {
    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
      url(./assets/offer-3.svg);

    padding: 50px 0 35px 35px;

    @media (max-width: $screen-xl-point) {
      padding: 106px 0 27px 27px;
    }

    & .offers__title {
      @include offers__title_xl_small;

      @media (max-width: $screen-xl-point) {
        @include offers__title_md;
      }
    }

    & .offers__subtitle {
      @include offers__subtitle_xl_small;

      @media (max-width: $screen-xl-point) {
        @include offers__subtitle_md;
      }
    }
  }

  &:nth-of-type(4) {
    grid-column-start: 1;
    grid-column-end: 3;

    @media (max-width: $screen-xl-point) {
      grid-column-start: 2;
      grid-column-end: 3;
      padding: 106px 0 27px 27px;
    }

    @media (max-width: $screen-md-point) {
      grid-column-start: 1;
      grid-column-end: 1;
    }

    background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(15, 20, 32, 0.79) 100%),
      url(./assets/offer-4.svg);

    & .offers__title {
      @include offers__title_xl;

      @media (max-width: $screen-xl-point) {
        @include offers__title_md;
      }
    }

    & .offers__subtitle { 
      @include offers__subtitle_xl;

      @media (max-width: $screen-xl-point) {
        @include offers__subtitle_md;
      }
    }
  }
}

.info {
  margin: 62px 80px;
  color: $white;

  @media (max-width: $screen-xl-point) {
    margin: 55px 30px 53px 29px;
  }

  @media (max-width: $screen-md-point) {
    margin: 50px 22px 50px 20px;
  }
}

.info__title {
  @include heading2;
}

.info__text {
  margin-top: 17px;
  opacity: 0.6;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
}


.read-more-label {
  display: block;
  margin-top: 17px;
  font-size: 14px;
  font-weight: 800;
  line-height: 17px;
  color: $yellow;

  &:hover {
    cursor: pointer;
    color: $transparentYellow;
  }
}

.button {
  padding: 14px 40px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  text-align: left;
  border-radius: 100px;
  outline: none;
  border: none;

  &:hover {
    cursor: pointer;
  }
}

.button_filled { 
  background-color: $yellow;

  &:hover {
    background-color: $transparentYellow;
  }
}

.button_outlined {
  background-color: transparent;
  border: 2px solid $yellow;
  color: $yellow;

  &:hover {
    opacity: 0.6;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 127px;

  background-color: $black;
}

.footer__title {
  @include paragraph;

  font-weight: 800;
  color: $yellow;
}


#header-mobile__hamburger-checkbox {
  display: none;
}

.header-mobile__hamburger-label {
  position: relative;
  display: flex; 
  align-items: center;  
  margin-right: 20px;
  width: 23px;
  height: 23px;
  cursor: pointer;

}

.header-mobile__hamburger-label > span,
.header-mobile__hamburger-label > span::before,
.header-mobile__hamburger-label > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 3px;
  background-color: $white;
}

.header-mobile__hamburger-label > span::before {
  content: '';
  top: -8px;
  transition-duration: 0.5s;
}

.header-mobile__hamburger-label > span::after {
  content: '';
  top: 8px;
  transition-duration: 0.5s;
}

.header-mobile__menu-list {
  position: fixed;
  visibility: hidden;
  top: 48px;
  left: 0;
  right: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: $black;
}

#header-mobile__hamburger-checkbox:checked ~ .header-mobile__hamburger-label > span {
  transform: rotate(45deg);
  transition-duration: 0.5s;
}

#header-mobile__hamburger-checkbox:checked ~ .header-mobile__hamburger-label > span::before {
  top: 0;
  transform: rotate(0);
  transition-duration: 0.5s;
}

#header-mobile__hamburger-checkbox:checked ~ .header-mobile__hamburger-label > span::after {
  top: 0;
  transform: rotate(90deg);
  transition-duration: 0.5s;
}

#header-mobile__hamburger-checkbox:checked ~ .header-mobile__menu-list {
  visibility: visible;
}

#header-mobile__hamburger-checkbox ~ .header-mobile__menu-item {
  height: 0;
}

.header-mobile__menu-list > .header-mobile__menu-item {
    &:hover {
      cursor: pointer;
  
      & .header-mobile__menu-item-text {
        background-clip: text;
        -webkit-text-fill-color: transparent;
        background-image: linear-gradient(98.25deg, $yellow 18.56%, $pink 76.02%);
      }
  
      & > .header__cart {
        background-image: url(./assets/header-gradient-cart.svg);
      }
    }
}

.header-mobile {
  display: none;

  @media (max-width: $screen-md-point) {
    display: block;
  }
}

@media (max-width: $screen-md-point) {
  #header-mobile__hamburger-checkbox ~ .header-mobile__menu-list >  .header-mobile__menu-item {
    margin: 0;
    padding: 0;
    height: 0;
    border: 0;
    transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
  }

  #header-mobile__hamburger-checkbox:checked ~ .header-mobile__menu-list >  .header-mobile__menu-item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 40px;
    padding: 0 15px;
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
  
    color: $white;

    transition: height 400ms cubic-bezier(0.33, 1, 0.42, 1);

    &:not(:last-child) {
      border-bottom: 1px solid $white;
    }
  }
}


.info__text_hidden {
  display: none;
}

#read-more-toggle {
  display: none;
}

#read-more-toggle:checked ~ .read-more-label {
  display: none;
}

#read-more-toggle:checked ~ .info__text_hidden {
  display: block;
}